<script>
	export default {
		data() {
			return {
				shows: [false, false, false]
			}
		},
		created() {},
		mounted(){
			this.init_baidutongji()
		},
		methods: {
			init_baidutongji(){
				 var hm = document.createElement("script");
				 hm.src = "https://hm.baidu.com/hm.js?2e3da497d639d918669700580383824f";
				 var s = document.getElementsByTagName("script")[0]; 
				 s.parentNode.insertBefore(hm, s);
				 //var s = document.getElementsByTagName("script")[0]; 
				 console.log("",s)
			},
			show(index) {
				//e.target.children[0].style="border: 3px solid blue;"
				//console.log(e.target.children[0].style)
				this.$set(this.shows, index, true);
			},
			leave(index) {
				this.$set(this.shows, index, false);
			},
			submitForm1(url, name) {
				var a = document.createElement('a')
				a.target = "_blank"
				if (name == "wiki") {
					a.href = url + this.$refs.input1.value
				} else if (name == "weixin") {
					a.href = url + this.$refs.input1.value
				} else {
					a.href = url + '?' + name + '=' + this.$refs.input1.value
				}
				a.click()
			}
		}
	}
</script>

