		border-radius: 5px;
		font-size: 13px;
		padding-left: 10px;
		box-sizing: border-box;

	}

	.shouye .serch_input1:focus {
		/* border: 2px solid green; */
		box-shadow: 0 0 0 2px rgba(16, 142, 233, .4);
	}

	.shouye .serch_buttton {
		box-sizing: border-box;
		height: 35px;
		width: 14%;
		line-height: 32px;
		border-radius: 2px;
		text-align: center;
		background: #69BDFB;
		border: none;
		outline: none;
		color: #fff;
	}

