		width: 850px;
		height: 280px;
		margin: 0px auto;
		background: rgba(090, 184, 227, .3);
		display: flex;
		overflow: hidden;
	}

	.shouye .lunbo_box .item {
		height: 280px;
		flex: 1;
		border-right: 1px solid rgba(090, 184, 227, .3);
		position: relative;
	}

	.shouye .lunbo_box .item:nth-child(1) {
		
	}
	.little_box_movedown {
		transform: translateY(100%);
	}

	.little_box_moveup {
		transform: translateY(0%);
	}

	.shouye .lunbo_box .little_box {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0px;
		transition: all 0.5s;
	}

	.shouye .lunbo_box .tittle {
		height: 50px;
		width: 70%;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 120px;
		text-align: center;
		line-height: 50px;
		border-bottom: 1px solid #fff;
		font-size: 20px;
	}

	.shouye .lunbo_box .info {
		height: 50px;
		width: 70%;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 50px;
		text-align: center;
		font-size: 10px;

	}

	.shouye .lunbo_box .show {
		height: 280px;
		transition: all 0.5s;
		flex: 1;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		align-content: center;
		justify-content: center;
		background: rgba(090, 184, 227, .1);
	}

	.cell {
		background: rgba(090, 184, 227, .0) !important;
	}

	.shouye .lunbo_box .show .cell {
		width: 45%;
		height: 30px;
		line-height: 30px;
		text-align: center;
		margin-left: 5px;
		margin-top: 5px;
		border: 1px solid rgba(090, 184, 227, .3);
		color: #fff;

	}

	.nomove {
		transform: translateY(-100%);
	}

	.move {
		transform: translateY(0%);
	}

	.cell:hover {
		background: #0876c2 !important;
		cursor: pointer;
	}

	.shouye .info_box {
		width: 850px;
		height: 280px;
		margin: 0px auto;
		display: flex;
		margin-top: 45px;
		background: rgba(090, 184, 227, .3);
	}

	.shouye .info_box_left {
		width: 200px;
		border-right: 1px solid rgba(090, 184, 227, .3);
	}

	.shouye .info_box_left .tittle {
